.feedback {
  min-height: 840px;
  padding: 150px 0;
  display: flex;
  border: none;
  background-image: url("../img/background/feedback-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/background/feedback-bg@2x.png");
    background-size: cover;
  }

  @include vp-1023 {
    background: none;
    background-color: $color-dodger-blue;
  }
}

.feedback__item {
  margin-left: auto;
  margin-right: 0;
  box-sizing: border-box;
  max-width: 620px;
  padding: 20px;

  @include vp-1023 {
    margin: 0 auto;
  }

  @include vp-767 {
    padding: 10px 0;
  }
}

